@import url("https://fonts.googleapis.com/css?family=Fredericka+the+Great|Special+Elite");
@import url("https://fonts.googleapis.com/css?family=Titillium+Web");
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.container-portada {
  position: relative;
  background-image: url("img/hai.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-animation: motion 20s infinite linear alternate;
          animation: motion 20s infinite linear alternate;
}

@-webkit-keyframes motion {
  from {
    background-position: top right;
  }
  to {
    background-position: bottom left;
  }
}

@keyframes motion {
  from {
    background-position: top right;
  }
  to {
    background-position: bottom left;
  }
}

.container-details {
  width: 100%;
  height: 100vh;
  max-width: 1200px;
  position: relative;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.container-details .details {
  width: 100%;
  max-width: 500px;
  position: relative;
  color: #070706;
}

.container-details .details h1 {
  font-size: 8em;
  font-weight: 400;
  margin-left: 10px;
  font-family: "Fredericka the Great", cursive;
}

.container-details .details h3 {
  font-size: 3.5em;
  font-weight: 400;
  margin-left: 10px;
  font-family: "Fredericka the Great", cursive;
}

.container-details .details p {
  margin-top: 30px;
  font-size: 20px;
  font-weight: bold;
  margin-left: 10px;
  font-family: "Titillium Web", sans-serif;
}

.container-details .details button {
  font-family: "Titillium Web", sans-serif;
  font-size: 1.5em;
  padding: 10px 20px;
  background: none;
  border-style: none;
  border: 1px solid #070706;
  color: #070706;
  margin-top: 20px;
  -webkit-transition: background 300ms;
  -o-transition: background 300ms;
  transition: background 300ms;
  cursor: pointer;
  margin-left: 10px;
}

.container-details .details hr {
  background-color: #e9dcdc;
  color: black;
  -webkit-text-decoration-line: underline overline;
          text-decoration-line: underline overline;
  border: black solid 1px;
}

.container-details .details a {
  text-decoration: none;
}

.details button:hover {
  background: transparent;
  color: #b44d75;
  border: 3px solid #b44d75;
}

/*============== About =============================*/
.navbar {
  position: fixed;
  background-color: rgba(22, 16, 16, 0.5);
  background: rgba(22, 16, 16, 0.5);
  border-color: rgba(22, 16, 16, 0.5);
  color: #3a3535;
}

.navbar li {
  color: white;
  font-family: "Titillium Web", sans-serif;
  font-size: 1.2em;
}

.navbar li a {
  color: white !important;
}

.hover:hover {
  font-size: 1.4em;
  font-weight: bold;
}

.about {
  width: 100%;
  height: 100vh;
  position: relative;
  background: white;
}

.details-about {
  margin-top: 2%;
  padding: 5px;
  text-align: center;
}

.details-about h3 {
  font-family: "Fredericka the Great", cursive;
  font-size: 10vmin;
  margin-top: 2%;
  color: #3a3535;
}

.details-about .about-text {
  margin-top: 5%;
}

.details-about .about-text p {
  font-size: 1.2em;
  font-family: "Titillium Web", sans-serif;
  padding: 15px;
}

.details-about .about-img {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-line-pack: center;
      align-content: center;
}

.lol {
  padding-top: 10px;
  padding-left: 10px;
}

/*=============================Skills===================================================*/
.skills {
  width: 100%;
  height: 100vh;
  position: relative;
}

.text-skills {
  margin-top: 4%;
  color: #070706 !important;
}

.text-skills h3 {
  font-family: "Fredericka the Great", cursive;
  font-size: 9vmin;
  margin: auto;
  color: #3a3535;
}

.text-skills p {
  font-family: "Titillium Web", sans-serif;
  font-size: 5vmin;
  color: black;
}

.container-information {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-line-pack: center;
      align-content: center;
  width: 100%;
}

.cont-i {
  margin-bottom: 2vmin;
  margin-top: 3vmin;
  width: 100%;
  height: 15vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

#i {
  position: absolute;
  margin-top: 50px;
  color: #222222 !important;
  font-size: 20vmin;
}

.cont-i:hover #i {
  color: #b44d75 !important;
}

/*===============================================Projects======================================*/
.projects {
  width: 100%;
  height: 100vh;
  position: relative;
}

.projects-cont {
  margin-top: 4%;
  color: #070706 !important;
}

.projects-cont h3 {
  font-family: "Fredericka the Great", cursive;
  font-size: 9vmin;
  margin: auto;
  color: #3a3535;
}

.projects-cont p {
  font-family: "Titillium Web", sans-serif;
  font-size: 5vmin;
  color: black;
}

.space-img {
  height: 30vh;
  margin: 3%;
}

.information {
  visibility: hidden;
  text-transform: uppercase;
  padding: 10px;
  position: absolute;
  top: 13px;
  left: 25px;
  width: 95%;
  height: 30vh;
  background: #b44d75;
  text-align: center;
  -webkit-transition: all 0.20s ease-in-out;
  -o-transition: all 0.20s ease-in-out;
  transition: all 0.20s ease-in-out;
}

.information h3 {
  color: white;
  font-size: 1.5em;
  font-family: "Fredericka the Great", cursive;
}

.information span {
  margin: 5%;
}

.information span a {
  text-decoration: none;
}

.information span i {
  color: white !important;
  border: 2px solid white;
  width: 80px;
  height: 8vh;
}

.project-div:hover .information {
  visibility: visible;
}

/*==================contactame===========================*/
.container-contacto {
  width: 100%;
  background-color: #222222;
  padding: 60px 0px 0px 0px;
}

.about-btn button {
  font-family: "Titillium Web", sans-serif;
  font-size: 1.5em;
  padding: 10px 20px;
  background: none;
  border-style: none;
  border: 1px solid white;
  color: white;
  margin-top: 10%;
  margin-bottom: 10%;
  -webkit-transition: background 300ms;
  -o-transition: background 300ms;
  transition: background 300ms;
  cursor: pointer;
  margin-left: 10px;
}

.about-btn a {
  text-decoration: none;
  color: #f7f0f0;
}

.about-btn button:hover {
  background: transparent;
  color: #b44d75;
  border: 3px solid #b44d75;
}

footer {
  text-align: center;
}

footer h3 {
  font-family: "Fredericka the Great", cursive;
  font-size: 10vmin;
  color: white;
}

footer p {
  font-family: "Titillium Web", sans-serif;
  font-size: 1.2em;
  color: white;
}

.footer-top {
  padding-top: 50px;
}

.footer-top .fa {
  color: #FFF;
  padding-right: 10px;
}

.footer-top a {
  color: #FFF;
  text-decoration: none;
}

.footer-top p {
  color: #FFF;
  font-family: "Titillium Web", sans-serif;
  font-size: 1.2em;
}

.footer-top .col-lg-2:hover .fa, .footer-top .col-lg-2:hover a, .footer-top .col-lg-2:hover p {
  color: #b44d75;
  padding-right: 4px;
}

/**********Media*******/
@media screen and (max-width: 384px) {
  .nav > li > a {
    text-align: center;
    color: white !important;
  }
  .navbar-default {
    background: black !important;
  }
  .navbar-default .navbar-nav > li > a:hover {
    border-bottom: none;
  }
  .collapsible-menu {
    background: rgba(77, 70, 70, 0.6);
  }
  /*Skills*/
  .text-skills {
    margin-top: 4%;
    color: #ebe9e9;
  }
  .text-skills h3 {
    font-family: "Fredericka the Great", cursive;
    font-size: 10vmin;
    margin: auto;
    color: #3a3535;
  }
  .text-skills p {
    font-family: "Titillium Web", sans-serif;
    font-size: 3vmin;
    color: black;
  }
  .text-skills .img-responsive {
    display: none;
  }
  .cont-i {
    margin-bottom: 5%;
    width: 100%;
    height: 15vw;
    background-color: transparent;
  }
  #i {
    position: absolute;
    color: black !important;
    font-size: 15vmin;
  }
  /*
@media only screen and (orientation: landscape) {
  .carousel-inner>.item>a>img, .carousel-inner>.item>img, .img-responsive, .thumbnail a>img, .thumbnail>img {
    display: none}
}
*/
}

@media screen and (max-width: 767px) {
  .carousel-inner > .item > a > img, .carousel-inner > .item > img, .lol2, .thumbnail a > img, .thumbnail > img {
    display: none;
  }
}

